import React, { Component } from "react";
import SearchBar from "./components/SearchBar";
import Container from "./components/Container";
import ImageGalleryList from "./components/ImageGallery";
import Modal from "./components/Modal";
import Button from "./components/Button";
import Spinner from "./components/Loader";
import ApiImg from "./services/apiImg";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./components/styles.css";

class App extends Component {
  state = {
    images: [],
    currentPage: 1,
    searchQuery: "",
    isLoading: false,
    error: null,
    showModal: false,
    largeImageURL: "",
    modalImage: "",
  };

  componentDidUpdate(prevProps, prevState) {
    if (prevState.searchQuery !== this.state.searchQuery) {
      this.fetchImg();
    }
  }

  onChangeQuery = (query) => {
    this.setState({
      searchQuery: query,
      images: [],
      currentPage: 1,
    });
    // console.log("onChangeQuery");
  };

  fetchImg = () => {
    const { currentPage, searchQuery } = this.state;
    const options = { searchQuery, currentPage };

    if (!searchQuery) {
      return;
    }

    this.setState({ isLoading: true });

    ApiImg.fetchImg(options)
      .then((hits) => {
        this.setState((prevState) => ({
          images: [...prevState.images, ...hits],
          currentPage: prevState.currentPage + 1,
        }));

        window.scrollTo({
          top: document.documentElement.scrollHeight,
          behavior: "smooth",
        });
      })
      .catch((error) => this.setState({ error }))
      .finally(() => {
        this.setState({ isLoading: false });
      });
  };

  openModal = (largeImageURL) => {
    this.setState({
      showModal: true,
      modalImage: largeImageURL,
    });
  };

  closeModal = () => {
    return this.setState({
      showModal: false,
      modalImage: "",
    });
  };

  render() {
    const { images, isLoading, error, showModal, modalImage } = this.state;
    const showLoadMoreBtn = images.length > 0 && !isLoading;

    return (
      <>
        <ToastContainer />

        <SearchBar onSubmit={this.onChangeQuery} />

        <Container>
          {error &&
            toast.warn(`Error is discipline through which we advance ${error}`)}

          {images.length > 0 && (
            <ImageGalleryList images={images} onImgClick={this.openModal} />
          )}

          {showLoadMoreBtn && (
            <Button nameBtn={"Load More"} onLoadMoreBtnClick={this.fetchImg} />
          )}

          {isLoading && <Spinner />}

          {showModal && (
            <Modal onClose={this.closeModal}>
              <img src={modalImage} alt="" />
            </Modal>
          )}
        </Container>
      </>
    );
  }
}

export default App;
